.footer {
  position: relative;
  display: flex;
  /* padding: 40px 0px 0px; */
  /* margin-top: 50px; */
  justify-content: center;
  background: linear-gradient(0deg,#000000, #24242441);
  background: linear-gradient(0deg, #2424247e, black);
  background: black;
  height: 160px;

  @media screen and (min-width: 768px) {
    height: 170px;
  };

  @media screen and (min-width: 1200px) {
    height: 180px;
  }
}

.footerContainer {
padding: 40px 0;
}

.borderDecorative {
  position: absolute;
  bottom: 200px;
  display: flex;
  z-index: 100;
  width: 100%;
  height: 1px;
  background: linear-gradient(90deg, #101010, #373737, #101010);
  background: linear-gradient(90deg,#000000, #2d1d01, #513401, #2d1d01, #000000);
  background: linear-gradient(90deg, #000000, #1f1f1f,#242424, #444444, #242424, #1f1f1f, #000000);
  /* background: linear-gradient(90deg, #060400, #3e3000, #060400); */
  background: linear-gradient(90deg, #000000, #1f1f1f,#242424, #444444, #242424, #1f1f1f, #000000);
  /* background-color: rgb(102, 51, 153); */

  @media screen and (min-width: 768px) {
    bottom: 160px;
  };

  @media screen and (min-width: 1200px) {
    bottom: 180px;
  }
}

.footerLogo {
 margin: 0;
 display: flex;
 justify-content: center;
}

.footerAllRights {
  display: flex;
  justify-content: center;
  color: #7b7b7b;
  margin-top: 30px;
}

.footerSocial {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 17px;
}

.footerSocial li img {
  height: 27px;
  /* box-shadow: 0 0 13px 6px #291a0fa3; */
  border-radius: 50%;
}

.footerList {
  display: grid;
  justify-content: center;
  align-items: center;
}

.footerList li{
  display: grid;
  justify-content: center;
  align-items: center;
}

.logoFooter {
 margin-top: 50px;
}
  
